.orderItem {
  width: 48%;
  border: 1px solid orange;
  border-radius: 15px;
  box-sizing: border-box;
  padding: 20px 20px 10px 20px;
  max-height: 140px;
  margin-right: 2%;
  margin-bottom: 20px;
  height: fit-content;
  transition: 0.5s all;
  &.active {
    max-height: 1000px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  & .more {
    text-align: end;
    &:hover {
      color: orange;
      cursor: pointer;
    }
  }
  & div {
    font-size: 20px;
  }
}
.buttonWrap {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid orange;
  & .str a {
    text-decoration: none;
    color: black;
    &:hover {
      cursor: pointer;
      color: orange;
    }
  }
}
