.personalData {
  width: 400px;
  &__input {
    display: flex;
    flex-direction: column;
    & label {
      font-size: 21px;
      margin: 6px 0;
    }
    & input {
      font-size: 21px;
      border-radius: 10px;
      padding: 5px;
      border: 1px solid rgb(226, 226, 233);
      &:active,
      &:focus-visible {
        outline: none;
        border: 1px solid orange;
      }
    }
  }
  &__button {
    width: 100%;
    font-size: 21px;
    padding: 10px 0;
    margin-top: 10px;
    background-color: orange;
    border-radius: 10px;
    &:hover {
      cursor: pointer;
    }
  }
}
