.profileHeader {
  width: 1280px;
  display: flex;
  align-items: center;
  margin: auto;
  height: 55px;
  justify-content: space-between;
  & .returnButton {
    display: flex;
    align-items: center;
    border: 1px solid gray;
    border-radius: 15px;
    padding: 0 10px 0 10px;
    background: white;
    height: 50px;
    box-sizing: border-box;
    &:hover {
      border: 1px solid orange;
      background-color: orange;
      cursor: pointer;
    }
  }
  & img {
    height: 50px;
    margin-right: 10px;
  }
}
